      fullMarkdown: 'BlankCat\n====\n\n\u5750\u6807\uFF1A\u5317\u4EAC\n\n\u8F6F\u4EF6\u9AD8\u7EA7\u5F00\u53D1\u5DE5\u7A0B\u5E08\n\n\u6280\u80FD\n====\n\n\u6570\u636E\u5E93\u8BBE\u8BA1\n----\n  - \u7ED3\u6784\u5316\u6570\u636E\u5E93\u8BBE\u8BA1\n  - noSQL\u8BBE\u8BA1\n\n\u540E\u7AEF\u5F00\u53D1\n----\n  - \u7528\u6237\u7BA1\u7406\n  - \u5355\u70B9\u767B\u5F55\n  - \u7B2C\u4E09\u65B9\u767B\u5F55\n  - \u6743\u9650\u7BA1\u7406\n  - \u4EA4\u6613\u7CFB\u7EDF\n  - \u652F\u4ED8\u7CFB\u7EDF\n  - \u535A\u5BA2\u7CFB\u7EDF\n  - \u516C\u4F17\u53F7\u5F00\u53D1\n  - API\u63A5\u53E3\u5F00\u53D1\n\n\u524D\u7AEF\u5F00\u53D1\n----\n  - Web\u524D\u7AEF\u5F00\u53D1\n  - \u79FB\u52A8\u7EC8\u7AEF(Native App, Hybrid App)\n\n\u4EA7\u54C1\u5F00\u53D1\n----\n  - \u5854\u8BFB\u6587\u5B66App\n  - \u70B9\u775B\u7F51\u4E91\u5E73\u53F0\n  - \u5F8B\u534F\u540E\u53F0\n  - \u5F8B\u6240\u4E2A\u4EBA\u540E\u53F0\n  - \u540D\u533B\u4F17\u79BE\u533B\u7597App\n  - \u5B9D\u846B\u82A6\u6570\u5B57\u533B\u7597\n  - \u7EA6\u4F60App\n\n\u6280\u672F\u53CA\u8BED\u8A00\n----\n  - Java: SpringMVC, SpringCloud, Hibernate, iBatis, freemarker, okHttp, retrofit, RxJava\n  - Kotlin: Android\n  - Node.js: express, angular, ionic, react, cordova,axios\n  - Golang: beego\n  - Python: django\n  - DB: SQLServer, Oracle, MySQL/MariaDB, MongoDB, redis, memcached\n  - WebServer: nginx, tomcat, netty, jetty\n  - OS: Ubuntu, CentOS, Windows\n  - Others: Docker, git, Xmind\uFF0CAxure\n\n\u5DE5\u4F5C\u7ECF\u5386\n----\n\n1. \u5317\u4EAC\u6613\u5929\u65B0\u52A8\u7F51\u7EDC\u79D1\u6280\u6709\u9650\u516C\u53F8(\u5854\u8BFB\u6587\u5B66)\n2. \u5317\u4EAC\u70B9\u775B\u5FEB\u4E50\u6559\u80B2\u79D1\u6280\u6709\u9650\u516C\u53F8(\u70B9\u775B\u7F51)\n3. \u660E\u533B\u4F17\u79BE\u79D1\u6280(\u5317\u4EAC)\u6709\u9650\u8D23\u4EFB\u516C\u53F8\uFF08\u660E\u533B\u4F17\u79BE\uFF09\n4. \u4E0A\u6D77\u950B\u7B97\u79D1\u6280\u6709\u9650\u516C\u53F8\uFF08\u5B9D\u846B\u82A6\u6570\u5B57\u533B\u7597\uFF09\n5. \u5317\u4EAC\u9886\u56E2\u79D1\u6280\u6709\u9650\u516C\u53F8\uFF08\u7EA6\u4F60App\uFF09\n\n\u6559\u80B2\u7ECF\u5386\n----\n\n1. \u5B89\u9633\u5E08\u8303\u5B66\u9662\n\n\u94FE\u63A5\n----\n\n* [GitHub](https://github.com/BlankCat)\n* [\u6280\u672F\u535A\u5BA2](http://blankcat.github.io/)\n* [\u4E2A\u4EBA\u4E3B\u9875](https://blankcat.github.io/myIndex/)\n* [\u7B80\u5386\u6A21\u677F\u53C2\u8003](https://github.com/sitexa/anires)\n\n\u52FE\u5F15\u65B9\u5F0F\n----\n\n* \u5FAE\u4FE1\uFF1AZJF2015ZJF\n\n', thanksMarkdown: '\n\u9E23\u8C22\n----\n\n* \u7279\u5907\u611F\u8C22[sitexa](https://github.com/sitexa)\u63D0\u4F9B\u7684\u6A21\u677F\u5730\u5740\n* \u5B66\u65E0\u6B62\u5883\uFF0C\u795D\u798F\u5728\u8DEF\u4E0A\u7684\u8FFD\u68A6\u4EBA\uFF01\n* \u6B22\u8FCE\u6765\u52FE\u642D\uFF0C\u975E\u8BDA\u52FF\u6270\uFF01\uFF08\u5FAE\u4FE1\uFF1AZJF2015ZJF\uFF09\n  '
